const useStyles = makeStyles((theme) => ({
  modal: {
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
  },
  paper: {
    backgroundColor: theme.palette.background.paper,
    border: "2px solid #000",
    boxShadow: theme.shadows[5],
    padding: theme.spacing(1, 7, 1),
  },
}));

export function ArtBoxWide(a) {
  const classes = useStyles();
  const [open, setOpen] = React.useState(false);

  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  return (
    <div className="ArtWide">
      <div className="ModalClick" onClick={handleOpen}>
        <img
          src={a.img}
          width="450px"
          height="253px;"
          alt="   "
        />
      </div>
      <div className="Artinfo">
        <p className="atitle"> : {a.title}</p>
        <p className="atitle"> : {a.program}</p>
        <p className="atitle"> : {a.length}</p>
        <p className="adescp">{a.adesc}</p>
      </div>
      <Modal
        className={classes.modal}
        open={open}
        onClose={handleClose}
        closeAfterTransition
        BackdropComponent={Backdrop}
        BackdropProps={{
          timeout: 100,
        }}
      >
        <Fade in={open}>
          <div className="ModalBox">
            <img
              src={a.modalImg}
              width="100%"
              alt="   "
            />
          </div>
        </Fade>
      </Modal>
    </div>
  );
}

export function ArtBoxDefault(a) {
  const classes = useStyles();
  const [open, setOpen] = React.useState(false);

  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  return (
    <div className="ArtBox">
      <div className="ModalClickS" onClick={handleOpen}>
        <img src={a.img} height="100%;" alt="   " />
      </div>
      <div className="Artinfo">
        <p className="atitle"> : {a.title}</p>
        <p className="atitle"> : {a.program}</p>
        <p className="atitle"> : {a.length}</p>
        <p className="adescp">{a.adesc}</p>
      </div>
      <Modal
        className={classes.modal}
        open={open}
        onClose={handleClose}
        closeAfterTransition
        BackdropComponent={Backdrop}
        BackdropProps={{
          timeout: 100,
        }}
      >
        <Fade in={open}>
          <div className="ModalBoxS">
            <img
              src={a.modalImg}
              width="100%"
              alt="   "
            />
          </div>
        </Fade>
      </Modal>
    </div>
  );
}

export function ArtBoxDefaulturl(a) {
  return (
    <div className="ArtBox">
      <div className="ModalClickS">
        <a href={a.url} target="_blank" rel="noreferrer">
          <img src={a.img} height="100%;" alt="   " />
        </a>
      </div>
      <div className="Artinfo">
        <p className="atitle"> : {a.title}</p>
        <p className="atitle"> : {a.program}</p>
        <p className="atitle"> : {a.length}</p>
        <p className="adescp">{a.adesc}</p>
      </div>
    </div>
  );
}

const ArtBoxYoutube = (a) => {
  return (
    <div className="ArtVideo">
      <Youtube
        className="Youtube_api"
        videoId={a.url}
        alt="Youtube API  "
      />
      <div>
        <p className="atitle"> : {a.title}</p>
        <p className="atitle"> : {a.program}</p>
        <p className="atitle"> : {a.length}</p>
        <p className="adescp">{a.adesc}</p>
      </div>
    </div>
  );
};

